@import url("https://fonts.googleapis.com/css?family=Ubuntu:300,400,500&subset=cyrillic,cyrillic-ext");
body {
  min-width: 1315px;
  margin: 0 auto;
  width: 100%;
  font-family: 'Ubuntu', sans-serif;
  background: #f5f7f8;
}

p, a, span {
  font-size: 15px;
  font-weight: 400;
  color: #444;
}

a {
  text-decoration: none;
}

input {
  padding-left: 10px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  height: 45px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
}

section {
  width: 100%;
  margin: 0 auto;
  display: inline-block;
  margin-bottom: 40px;
}

h2 {
  font-size: 36px;
  font-weight: 500;
  color: #444;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  background: transparent;
  transition: background .5s ease-out;
  z-index: 0;
}

.overlay.show {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  transition: background .5s ease-out;
  z-index: 5;
}

.btn {
  height: 45px;
  cursor: pointer;
}

.two-wrap {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.two-wrap .left, .two-wrap .right {
  width: 50%;
}

ul li label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 15px;
  color: #444;
}

ul li input[type="checkbox"] + label::before {
  content: "";
  display: inline-block;
  height: 17px;
  width: 17px;
  margin-right: 20px;
  background-image: url("../img/check-sprite.png");
  background-repeat: no-repeat;
}

ul li input[type="checkbox"] + label:before {
  background-position: 0;
}

ul li input[type="checkbox"]:checked + label:before {
  background-position: -18px 0;
}

ul li input[type="checkbox"]:disabled + label:before {
  background-position: 0;
}

ul li input[type="checkbox"]:checked:disabled + label:before {
  background-position: -18px 0;
}

ul li label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 15px;
  color: #444;
}

ul li input[type="radio"], ul li input[type="checkbox"] {
  display: none;
}

ul li input[type="radio"] + label::before {
  content: "";
  display: inline-block;
  height: 17px;
  width: 17px;
  margin-right: 20px;
  background-image: url("../img/radio-sprite.png");
  background-repeat: no-repeat;
}

ul li input[type="radio"] + label:before {
  background-position: 0;
}

ul li input[type="radio"]:checked + label:before {
  background-position: -19px 0;
}

ul li input[type="radio"]:disabled + label:before {
  background-position: 0;
}

ul li input[type="radio"]:checked:disabled + label:before {
  background-position: -18px 0;
}

.noselect-wrap {
  position: fixed;
  top: 25%;
  left: 50%;
  width: 600px;
  max-height: 300px;
  margin-left: -20%;
  background: #ffffff;
  z-index: 9;
  padding: 15px 20px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.noselect-wrap .head {
  margin-bottom: 10px;
}

.noselect-wrap .head h2 {
  font-size: 25px;
  text-align: center;
  color: #444;
  margin-bottom: 10px;
}

.noselect-wrap .head p {
  font-size: 14px;
  text-align: center;
}

.noselect-wrap .main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 15px 25px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.noselect-wrap .main div {
  width: 100%;
  text-align: center;
}

.noselect-wrap .main select {
  width: 80%;
  height: 45px;
}

.noselect-wrap .footer {
  text-align: center;
}

.noselect-wrap .footer .btn {
  background: #48a2ff;
  border: 1px solid #2d92fa;
  padding: 0 25px;
  color: #fff;
}

.shops-popup-wrap {
  position: fixed;
  top: 25%;
  left: 50%;
  width: 600px;
  height: 200px;
  margin-left: -15%;
  background: #ffffff;
  z-index: 7;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.shops-popup-wrap .close {
  position: absolute;
  top: 15px;
  right: 10px;
  display: inline-block;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.shops-popup-wrap .close:before, .shops-popup-wrap .close:after {
  position: absolute;
  left: 9px;
  content: ' ';
  height: 20px;
  width: 2px;
  background-color: #d0d4dc;
}

.shops-popup-wrap .close:before {
  transform: rotate(45deg);
}

.shops-popup-wrap .close:after {
  transform: rotate(-45deg);
}

.shops-popup-wrap h2 {
  margin-bottom: 15px;
}

.shops-popup-wrap h2, .shops-popup-wrap p {
  text-align: center;
}

.unauthorized-user-popup {
  position: fixed;
  top: 25%;
  left: 50%;
  width: 600px;
  height: 140px;
  margin-left: -15%;
  background: #ffffff;
  z-index: 7;
}

.unauthorized-user-popup .head {
  height: 50px;
  line-height: 50px;
  position: relative;
  margin-bottom: 15px;
}

.unauthorized-user-popup .head h2 {
  text-align: center;
  font-size: 21px;
}

.unauthorized-user-popup .head .close {
  position: absolute;
  top: 15px;
  right: 10px;
  display: inline-block;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.unauthorized-user-popup .head .close:before, .unauthorized-user-popup .head .close:after {
  position: absolute;
  left: 9px;
  content: ' ';
  height: 20px;
  width: 2px;
  background-color: #d0d4dc;
}

.unauthorized-user-popup .head .close:before {
  transform: rotate(45deg);
}

.unauthorized-user-popup .head .close:after {
  transform: rotate(-45deg);
}

.unauthorized-user-popup .main {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.unauthorized-user-popup .main .btn {
  background: #48a2ff;
  border: 1px solid #2d92fa;
  padding: 0 20px;
  text-align: center;
  line-height: 45px;
  font-size: 16px;
  color: #fff;
}

